.App {
  text-align: center;
  display: flex;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

h1 {
  color: #87AAAA;
}

#logo {
  display: block;
  justify-content: center;
}

#nav {
  padding: 10px 10px 10px 10px;
  border-bottom: solid 2px #F6EABE;
  margin-bottom: 20px;
}

#form-autocomplete {
  display: inline-block;
  padding-right: 20px;
}

#ticker-search {
  display: inline-block;
}

